function Sliders() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".slider")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let isLoop = slider.classList.contains("_loop")
        let isLoopMob = slider.classList.contains("_loop-mob")

        SlidersArray[index] = new Swiper(`.slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            loop: isLoop,
            navigation: {
                nextEl: `.slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
            scrollbar: {
                el: `.slider[data-slider-id="${index}"] .swiper-scrollbar`,
                draggable: true,
                snapOnRelease: true,
                dragSize: 'auto',
            },
            breakpoints: {
                320: {
                    loop: isLoopMob,
                },
                992: {
                    loop: isLoop,
                }
            }
        })
    })
}

function gallery() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".gallery")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let gallery = {
            mySwiper: {},
            mySwiper2: {}
        }

        gallery.mySwiper = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper`, {
            spaceBetween: 0,
            slidesPerView: "auto",
            freeMode: true,
            watchSlidesProgress: true,
            direction: "vertical",
            breakpoints: {
                320: {
                    direction: "horizontal",
                },
                992: {
                    direction: "vertical",
                }
            }
        });
        gallery.mySwiper2 = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper2`, {
            spaceBetween: 0,
            direction: "vertical",
            loop: true,
            navigation: {
                nextEl: `.gallery[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.gallery[data-slider-id="${index}"] .swiper-button-prev`,
            },
            thumbs: {
                swiper:  gallery.mySwiper,
            },
            breakpoints: {
                320: {
                    direction: "horizontal",
                },
                992: {
                    direction: "vertical",
                }
            }
        });

        SlidersArray[index] = gallery
    })

}

function catalogSliders() {
    let swiper2 = new Swiper('.main-slider2 .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        watchSlidesProgress: true,
        preventClicks :true,
        a11y: false,
        observer: true,
        observeParents: true,
        observeSlideChildren: true,
        loop: true,
        loopAdditionalSlides: 7,
        navigation: {
            nextEl: '.main-slider2 .swiper-button-next',
            prevEl: '.main-slider2 .swiper-button-prev',
        },
        scrollbar: {
            el: `.main-slider2 .swiper-scrollbar`,
            draggable: true,
            snapOnRelease: true,
            dragSize: 'auto',
            hide: false,
        },
        breakpoints: {
            320: {
                loopAdditionalSlides: 1,
                // loop: false
            },
            576: {
                loopAdditionalSlides: 3,
                // loop: false
            },
            992: {
                loopAdditionalSlides: 7,
                loop: true
            }
        },
        on: {
            slideChange: function () {

            },
        },
    })


    let swiper = new Swiper('.main-slider .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        watchSlidesProgress: true,
        preventClicks :true,
        a11y: false,
        observer: true,
        observeParents: true,
        observeSlideChildren: true,
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        navigation: {
            nextEl: '.main-slider .swiper-button-next',
            prevEl: '.main-slider .swiper-button-prev',
        },
        scrollbar: {
            el: `.main-slider .swiper-scrollbar`,
            draggable: true,
            snapOnRelease: true,
            dragSize: 'auto',
        },
        on: {
            init: function () {
                let slideId = this.activeIndex
                swiper2.slideTo(slideId+1)
            },
            slideChange: function () {
                let slideId = this.activeIndex
                swiper2.slideTo(slideId+1)
            },
        },
    })
}


Sliders()
gallery()
catalogSliders()
